import styles from "./Contact.module.css";

import { useRef } from "react";
import emailjs from "@emailjs/browser";
import { Form, Input, TextArea, Button } from "semantic-ui-react";
import Swal from "sweetalert2";
import Fade from "react-reveal/Fade";
export function ContactForm({setIsAuth}) {
  const form = useRef();

  const handleOnSubmit = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_o87nnal",
        "template_ig7lakc",
        e.target,
        "v6XbrIZLnDEV-IVZ-"
      )
      .then(
        (result) => {
          console.log(result.text);
          Swal.fire({
            icon: "error",
            title: "Ooops, something went wrong",
            // text: error.text,
          });
        },
        (error) => {
          console.log(error.text); 
          Swal.fire({
            icon: "success",
            title: "Message Sent Successfully",
          });
        }
      );
    e.target.reset();
  };
  return (
    <Fade bottom>
    <div className={styles.form} >
   
        <p className={styles.contactHeading}>Contact Me <hr  width="22%" color="gray"/></p>
     
       
        <div className="App">
          <Form onSubmit={handleOnSubmit}>
            <input
              label="Email"
              className={styles.feedback_input}
              name="email"
              placeholder="Email…"
              required
            />
            <input
              className={styles.feedback_input}
              label="Name"
              name="from_name"
              placeholder="Name…"
              required
            />
            <input
              className={styles.feedback_input}
              control={TextArea}
              label="Message"
              name="message"
              placeholder="Message…"
              required
            />
          
            <button onClick={ ()=>setIsAuth(false)} type="back" >
              Back
            </button>
            <button type="submit" >
              Send
            </button>
          </Form>
        </div>
        
    </div>
    
    </Fade>
  );
}